import './Widgets/Measure/Measure.css'
import './Widgets/TransformEditor/TransformEditor.css'
import './cesium-ion-sdk-plugin.css'

import MeasureUnits from './Widgets/Measure/MeasureUnits'
import DistanceUnits from './Widgets/Measure/DistanceUnits'
import AreaUnits from './Widgets/Measure/AreaUnits'
import VolumeUnits from './Widgets/Measure/AreaUnits'
import TransformEditor from './Widgets/TransformEditor/TransformEditor'
import viewerMeasureMixin from './Widgets/Viewer/viewerMeasureMixin';
import DistanceMeasurement from './Widgets/Measure/DistanceMeasurement';
import MeasurementMouseHandler from './Widgets/Measure/MeasurementMouseHandler';

import PolylineMeasurement from './Widgets/Measure/PolylineMeasurement';
import HorizontalMeasurement from './Widgets/Measure/HorizontalMeasurement';
import VerticalMeasurement from './Widgets/Measure/VerticalMeasurement';
import HeightMeasurement from './Widgets/Measure/HeightMeasurement';
import AreaMeasurement from './Widgets/Measure/AreaMeasurement';
import PointMeasurement from './Widgets/Measure/PointMeasurement';

function enableCesiumIonSDK() {
    window.Cesium.MeasurementMouseHandler = MeasurementMouseHandler;
    window.Cesium.DistanceMeasurement = DistanceMeasurement;
    window.Cesium.PolylineMeasurement = PolylineMeasurement;
    window.Cesium.HorizontalMeasurement = HorizontalMeasurement;
    window.Cesium.VerticalMeasurement = VerticalMeasurement;
    window.Cesium.HeightMeasurement = HeightMeasurement;
    window.Cesium.AreaMeasurement = AreaMeasurement;
    window.Cesium.PointMeasurement = PointMeasurement;

    window.Cesium.MeasureUnits = MeasureUnits;
    window.Cesium.DistanceUnits = DistanceUnits;
    window.Cesium.AreaUnits = AreaUnits;
    window.Cesium.VolumeUnits = VolumeUnits;
    window.Cesium.TransformEditor = TransformEditor;
    window.Cesium.viewerMeasureMixin = viewerMeasureMixin;
}

export {
    enableCesiumIonSDK
}
